import React, { useRef, FC } from 'react';
import clsx from 'clsx';
import styles from './constructor-element-wrap.module.css';

import {
  ConstructorElement,
  DragIcon,
} from '@ya.praktikum/react-developer-burger-ui-components';
import { useDrag, useDrop } from 'react-dnd/dist/hooks';

import { TIngredient, useAppDispatch } from '../../../services/types/types';
import { DELETE_INGREDIENTS } from '../../../services/actions/app';

type TConstructorElementWrap = {
  index?: number;
  item: TIngredient;
  text?: string;
  type?: 'top' | 'bottom' | undefined;
  isLocked?: boolean;
  moveCard?: (dragIndex: number, hoverIndex: number) => void;
};

const ConstructorElementWrap: FC<TConstructorElementWrap> = ({
  index,
  item,
  text,
  type,
  isLocked,
  moveCard,
}) => {
  const classForList = clsx(styles.item, 'mr-1 ml-4 pl-8');
  const dispatch = useAppDispatch();
  const positionText = item.name + (text !== undefined ? text : '');

  const onDelete = (dragId?: string) => {
    dispatch({
      type: DELETE_INGREDIENTS,
      item: item,
      dragId: item.dragId,
    });
  };

  const ref = useRef<HTMLLIElement>(null);

  const [{ handlerId }, drop] = useDrop({
    accept: 'component',
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId(),
      };
    },
    // ,      ,
    //        props.index
    hover(item: { id: string; index: number } | any, monitor) {
      if (!ref.current) {
        return;
      }
      //     
      const dragIndex = item.index;
      const hoverIndex = index;
      //   ,     
      if (dragIndex === hoverIndex) {
        return;
      }
      //    
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      //      Y  
      //     ,  
      //     
      const hoverMiddleY =
        (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      //    ,
      //   
      const clientOffset = monitor.getClientOffset();
      //       
      //       
      if (
        clientOffset !== null &&
        hoverIndex !== undefined &&
        moveCard !== undefined
      ) {
        const hoverClientY = clientOffset.y - hoverBoundingRect.top;
        //      
        //     
        //  ,       moveCard
        if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
          return;
        }
        //      
        //      ,    
        if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
          return;
        }
        //        
        moveCard(dragIndex, hoverIndex);
        //      
        //     
        item.index = hoverIndex;
      }
    },
  });

  //       
  //  
  const [{ isDragging }, drag] = useDrag({
    type: 'component',
    item: () => ({ id: item._id, index }),
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });
  const opacity = isDragging ? 0 : 1;

  //           :)
  if (item.type !== 'bun') drag(drop(ref));
  //     onDrop
  //           
  const preventDefault = (e: React.SyntheticEvent) => e.preventDefault();

  return (
    <li
      className={classForList}
      ref={ref}
      style={{ opacity }}
      onDrop={preventDefault}
      data-handler-id={handlerId}
    >
      {type !== 'top' && type !== 'bottom' && (
        <div className={styles.icon}>
          <DragIcon type="primary" />
        </div>
      )}
      <ConstructorElement
        type={type}
        isLocked={isLocked}
        handleClose={() => {
          onDelete(item.dragId);
        }}
        text={positionText}
        price={item.price !== undefined ? item.price : 0}
        thumbnail={item.image !== undefined ? item.image : ''}
      />
    </li>
  );
};

export default React.memo(ConstructorElementWrap);
